.home {
  background-color: #f9f9f9;
  width: 100%;
  margin: 20px;
}

.input {
  box-shadow: 0px 8px 8px rgba(189, 20, 20, 0.644);
}
.About {
  margin-right: 30px;
  margin-left: 60px;
  border-bottom: 1px solid lightgray;
  align-items: center;
  justify-content: center;
}
.Rooms {
  margin-right: 30px;
  margin-left: 60px;
  border-bottom: 1px solid lightgray;
  width: 100%;
  height: auto;
}
.help {
  margin: 20px;
}
.email {
  color: rgb(252, 252, 134);
}
.phone {
  color: rgb(252, 252, 134);
}

.title {
  display: flex;
  margin: 20px;
}
.hotel {
  color: rgba(216, 13, 13, 0.753);
}
.hotel2 {
  color: rgba(216, 13, 13, 0.753);
  margin: 20px;
}
.contact {
  color: white;
  width: 300px;
}
.room1 > img {
  background-color: lightgray;
  width: 100%;
  height: 80%;
  max-width: 100%;
  max-height: 80%;
  transform: scale(1.2);
  transition: all 0.5s ease;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  transition-delay: 0s;
  --webkit-backface-visibility: hidden;
  overflow: hidden;
}
.room1 {
  margin: 30px;
  width: 359px;
  height: 400px;
  overflow: hidden;
  border-radius: 33px;
  box-shadow: 0px 8px 8px rgba(59, 37, 37, 0.644);
}
.room {
  display: flex;
}
.hall {
  display: flex;
}
.room_name {
  margin-top: 30px;
}
.p_tag {
  font-size: 20px;
  margin-left: 19px;
}
.image:hover {
  overflow: hidden;
  transform: scale(1);
  --webkit-transition: all 0.5s ease;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  transition-delay: 0s;
  backface-visibility: hidden;
}
.room_name {
  margin-left: 20px;
}
.reasons {
  width: 300px;
  height: 340px;
  padding: 30px;
  justify-content: center;
  margin: 20px;
  border-radius: 33px;
  overflow: scroll;
  position: relative;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.459);
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center;
}

.home > img {
  width: 100%;
}
.reasons2 {
  display: flex;
}

.Rooms {
  margin: 0px;
}

@media only screen and (max-width: 800px) {
  .room {
    flex-direction: column;
    margin-left: none;
    width: 80%;
  }
  .title2 {
    margin: 0px;
    font-size: 8px;
  }
  .Rooms {
    margin: 0px;
  }
  .room1 {
    margin-left: 0px;
  }
  .reasons {
    width: 404px;
    height: 300px;
    flex-direction: column;
  }
  .reasons2 {
    flex-direction: column;
    margin: 0px;
  }
}

.contact {
  background-image: url("bg.png");
}
.font {
  font-size: 20px;
  font-weight: 0px;
}
.services {
  text-align: center;
  margin: 30px;
}
.p {
  margin: 20px;
}
.header_nav {
  display: flex;
  flex-direction: row;
}
