.header {
  height: 60px;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.575);
  border-radius: 20px;
  width: 100%;
}
.header_logo {
  width: 60px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 5px;
}

.header_search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}
.header_input {
  height: 12px;
  padding: 10px;
  border: 1px solid pink;
  width: 100%;
}
.fsr {
  font-family: "Caveat", cursive;
}
.sliderimg {
  width: 105%;
  height: 100%;
}

.fsr2 {
  margin-top: 20px;

  font-family: "Gochi Hand", cursive;
}
.fsr3 {
  margin-top: 40px;
  margin-right: 20px;
  font-family: "Texturina", serif;
}

.header_searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #cd9042;
}

.header_option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  text-decoration: none;
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
  color: white;
}

.header_optionLineOne {
  font-size: 10px;
  text-decoration: none;
  color: black;
  text-decoration: underline none;
  font-size: 15px;
}
.header_optionLineOne:hover {
  font-size: 10px;
  text-decoration: none;
  color: red;
  text-decoration: underline none;
  font-size: 15px;
}
.header_optionLineTwo > a {
  font-size: 13px;
  font-weight: 800;
  text-decoration: none;
}
.header_optionBasket {
  display: flex;
  align-items: center;
  color: pink;
}

.header_basketCount {
  margin-left: 10px;
  margin-right: 10px;
}

@media only screen and (max-width: 700px) {
  .header_option {
    display: none;
  }

  h3 {
    margin-bottom: 10px;
  }
}
